/* Box Shadow */
/* GLOBAL STYLES */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  background-color: #EBE7DE;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.5;
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 3rem;
}

h2 {
  color: #5E311E;
  font-size: 2.8rem;
  margin-bottom: 2rem;
}
h2 span {
  display: block;
  font-size: 1.8rem;
  color: #C39C63;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.6rem;
  }
}
h2::after {
  content: "";
  width: 10rem;
  height: 10rem;
  display: block;
  margin: 0 auto;
  background-image: url("../images/cafe.svg");
  background-size: 10rem;
}

h3 {
  font-size: 2.5rem;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

p {
  color: #000;
  font-size: 1.8rem;
  line-height: 2;
}

label {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2rem;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  width: 95%;
}

/* HEADER SECTION */
.header {
  padding: 5rem 0;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-color: #000;
  opacity: 0.6;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.header .header-content {
  position: relative;
}
.header .header-content h1 {
  text-align: center;
  font-size: 2.2rem;
  color: #FFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 6rem 0 10rem 0;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .header .header-content h1 {
    font-size: 4rem;
    margin: 10rem 0;
  }
}

.header-index {
  background-image: url("../images/header_inicio.jpg");
}

.header-about {
  background-image: url("../images/header_nosotros.jpg");
}

.header-process {
  background-image: url("../images/header_proceso.jpg");
}

.header-menu {
  background-image: url("../images/header_menu.jpg");
}

.header-gallery {
  background-image: url("../images/header_galeria.jpg");
}

.header-contact {
  background-image: url("../images/header_contacto.jpg");
}

/* NAVBAR */
.navbar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.navbar .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .navbar .logo {
    width: 20rem;
    margin: unset;
  }
}

/* NAVBAR LINKS */
.links {
  display: flex;
  flex-direction: column;
  margin-top: 2.5rem;
}
.links a {
  text-align: center;
  color: #FFF;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.links a:hover {
  color: #C39C63;
}
.links a.active {
  color: #C39C63;
}
@media (min-width: 768px) {
  .links {
    flex-direction: row;
    margin-top: 0;
  }
  .links a {
    margin: 0 0.7rem;
  }
  .links a:last-child {
    margin-right: 0;
  }
}

/* FOOTER */
.footer {
  background-color: #5E311E;
}

.footer-content {
  padding: 2rem 2.5rem 5rem 2.5rem;
  position: relative;
}
@media (min-width: 768px) {
  .footer-content {
    display: grid;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 5rem 2.5rem;
  }
}

.footer-box {
  margin-bottom: 5rem;
}
.footer-box h3 {
  color: #C39C63;
  font-family: "Bona Nova", serif;
  margin-bottom: 1rem;
}
.footer-box p {
  font-family: "Bona Nova", serif;
  font-size: 2rem;
  line-height: 1.5;
  text-align: center;
}

.copyright {
  padding: 2.5rem;
  font-size: 1.6rem;
  color: #FFF;
}

/* MAIN SECTION */
.main-content {
  padding: 5rem 2.5rem;
}
@media (min-width: 768px) {
  .main-content {
    padding: 10rem;
  }
}

/* ICONS */
.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons .icon-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
  }
}
.icons .icon-list li img {
  width: 12rem;
  margin: 0 auto;
}
.icons .icon-list li p {
  font-family: "Bona Nova", serif;
  color: #5E311E;
  font-size: 3.8rem;
  margin: 3.2rem 0;
}

/* GALLERY */
.gallery-content {
  margin-top: 5rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 992px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery .gallery-image img {
  width: 30rem;
  height: 30rem;
  margin: 0 auto;
  object-fit: cover;
}
@media (min-width: 992px) {
  .gallery .gallery-image:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery .gallery-image:nth-child(1) img {
    height: 100%;
  }
  .gallery .gallery-image:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery .gallery-image:nth-child(2) img {
    width: 100%;
  }
  .gallery .gallery-image:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery .gallery-image:nth-child(4) img {
    height: 100%;
  }
  .gallery .gallery-image:nth-child(5), .gallery .gallery-image:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery .gallery-image:nth-child(5) img, .gallery .gallery-image:nth-child(6) img {
    width: 100%;
  }
  .gallery .gallery-image:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery .gallery-image:nth-child(7) img {
    height: 100%;
  }
  .gallery .gallery-image:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery .gallery-image:nth-child(8) img {
    width: 100%;
  }
  .gallery .gallery-image:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery .gallery-image:nth-child(9) img {
    width: 100%;
  }
  .gallery .gallery-image:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery .gallery-image:nth-child(10) img {
    width: 100%;
  }
}

/* MENU SECTION */
.menu {
  padding: 13rem 2.5rem 20rem 2.5rem;
  background-image: url("/build/images/menu_arriba.svg"), url("/build/images/menu_abajo.svg"), linear-gradient(to top, #5E311E, #5E311E);
  background-position: center 3.5rem, center 98%, 100%;
  background-repeat: no-repeat;
  background-size: 200%;
  position: relative;
}
@media (min-width: 768px) {
  .menu {
    background-position: center 3.5rem, center 90%, 100%;
    padding: 15rem 0 27rem 0;
    background-size: 100%;
  }
}
@media (min-width: 992px) {
  .menu {
    background-size: 50%, 50%, 100%;
  }
}
.menu h2 {
  color: #FFF;
}
.menu h2 span {
  color: #FFF;
}
.menu h2::after {
  display: none;
}

@media (min-width: 768px) {
  .menu-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media (min-width: 768px) {
  .menu-column {
    margin-right: 1rem;
  }
  .menu-column:last-child {
    margin-right: 0;
  }
}
.menu-column h3 {
  color: #FFF;
  font-size: 3.2rem;
}
.menu-column h3:last-of-type {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .menu-column h3:last-of-type {
    margin-top: 0;
  }
}
.menu-column li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu-column li p {
  color: #FFF;
  font-size: 2.2rem;
  line-height: 1.5;
  margin-bottom: 2.5rem;
}
.menu-column li p span {
  display: block;
  color: #C39C63;
}
.menu-column li .precio {
  font-weight: 700;
  font-size: 4rem;
}

/* TESTIMONIAL SECTION */
.testimonial-card {
  background-color: #EBE7DE;
  padding: 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}
.testimonial-card p {
  font-size: 1.6rem;
  line-height: 2;
}
.testimonial-card .author {
  font-family: "Bona Nova", serif;
  font-size: 2.4rem;
  color: #5E311E;
  margin-top: 2rem;
  text-align: right;
}

/* ABOUT US VIEW */
.about-content {
  margin-top: 5rem;
}
@media (min-width: 992px) {
  .about-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.about-content .about-image {
  max-width: 50rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 992px) {
  .about-content .about-image {
    max-width: unset;
    margin-bottom: 0;
    margin-right: 1rem;
  }
}
.about-content .about-image img {
  transform: rotate(-4.2deg);
  -webkit-transform: rotate(-4.2deg);
  -moz-transform: rotate(-4.2deg);
  -ms-transform: rotate(-4.2deg);
  -o-transform: rotate(-4.2deg);
  border: 2.5rem solid #FFF;
  box-shadow: 2px 3px 5px 1.5px rgba(0, 0, 0, 0.4);
}
.about-content .about-text {
  padding: 1rem 2rem;
}
.about-content .about-text blockquote {
  padding: 1rem;
  margin: 1rem 0;
  font-size: 2rem;
  border-left: 5px solid #5E311E;
}

/* PROCESS VIEW */
.process-card {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .process-card {
    display: flex;
    align-items: center;
  }
}
.process-card .card-image,
.process-card .card-text {
  flex: 1;
}
@media (min-width: 768px) {
  .process-card .card-image {
    margin-right: 3rem;
  }
  .process-card .card-image img {
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
@media (min-width: 768px) {
  .process-card:nth-child(even) {
    flex-direction: row-reverse;
  }
  .process-card:nth-child(even) .card-image {
    margin-right: 0;
    margin-left: 3rem;
  }
}
.process-card .card-text h3 {
  color: #5E311E;
  font-size: 2.8rem;
  margin: 2rem 0;
  text-align: left;
}
.process-card .card-text h3::after {
  content: "";
  display: block;
  width: 160px;
  height: 3px;
  background-color: #C39C63;
}

.menu-info {
  background-image: url("../images/cafe.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/4.5;
}
@media (min-width: 768px) {
  .menu-info {
    aspect-ratio: 10/12;
  }
}
@media (min-width: 992px) {
  .menu-info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    aspect-ratio: 16/6;
    background-size: 45%;
    background-position: bottom right;
  }
}
@media (min-width: 992px) {
  .menu-info .menu-text {
    text-align: left;
  }
  .menu-info .menu-text p {
    font-size: 2rem;
  }
}

.contact-content {
  padding: 3rem 2rem;
}
@media (min-width: 768px) {
  .contact-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    background-image: url("../images/granos_cafe.jpg");
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: center right;
  }
}
@media (min-width: 992px) {
  .contact-content {
    background-size: 40%;
  }
}
.contact-content .input {
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.contact-content .input input,
.contact-content .input select {
  width: 100%;
  padding: 0.7rem 1rem;
  border: 4px solid #5E311E;
}
.contact-content .input select {
  background-color: #FFF;
}
.contact-content .input input[type=submit] {
  background-color: #5E311E;
  color: #FFF;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
}
.contact-content .input input[type=submit]:hover {
  background-color: #683823;
}

/* UTITLITIES */
.py-5 {
  padding: 5rem 0;
}

.m-0 {
  margin: 0;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5E311E;
}

.bg-light {
  background-color: #FFF;
}

.negative-margin-10 {
  padding: 8rem 2.5rem;
  margin-top: -10rem;
  position: relative;
}
@media (min-width: 768px) {
  .negative-margin-10 {
    padding: 10rem 8rem;
  }
}

.border-top-radius {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.border-bottom-radius {
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.padding-bottom-0 {
  padding-bottom: 0;
  margin-bottom: -1px;
}